.arrow {
  width: 36px;
  height: 36px;
  background-color: #818181;
  border-radius: 30px;
  position: absolute;
  cursor: pointer;
}

.arrow-right {
  top: 46%;
  right: -60px;
  transition: all 0.3s linear;
}

.arrow-right span {
  display: block;
  height: 18px;
  width: 18px;
  border-top: 2px solid #fff;
  border-right: 2px solid #fff;
  transform: rotate(45deg);
  position: absolute;
  top: 9px;
  left: 6px;
  transition: all 0.3s linear;
}

.arrow-right:hover span {
  border-color: #818181;
}

.arrow-right:hover {
  background-color: #ffffff;
}

.arrow-left {
  top: 46%;
  left: -60px;
  transition: all 0.3s linear;
}

.arrow-left span {
  display: block;
  height: 18px;
  width: 18px;
  border-top: 2px solid #fff;
  border-left: 2px solid #fff;
  transform: rotate(-45deg);
  position: absolute;
  top: 9px;
  right: 6px;
  transition: all 0.3s linear;
}

.arrow-left:hover span {
  border-color: #818181;
}

.arrow-left:hover {
  background-color: #ffffff;
}

