.header {
  margin: 0 auto;
  background-image: url('../img/header__bg.jpg');
  background-position: center center;
  background-size: cover;

  width: 90%;

  &__info {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
  }

  &__info-title {
    color: #151515;
    font-size: 56px;
    font-family: 'Montserrat', sans-serif;
    line-height: 1.25;
    font-weight: 700;
    padding-bottom: 20px;
  }

  &__btn {
    display: block;
    background-color: #ffcf00;
    box-shadow: 0px 3px #a18400;
    border-radius: 10px;
    width: 400px;
    max-width: 100%;
    margin: 0 auto 5px 0;
  }

  &__btn a {
    color: #151515;
    font-size: 18px;
    font-family: 'Montserrat', Arial, sans-serif;
    line-height: 1.55;
    font-weight: 700;
    transition: background-color 0.2s ease-in-out, color 0.2s ease-in-out, border-color 0.2s ease-in-out;
    text-align: center;
    display: block;
    padding: 15px 50px
  }

  &__btn:hover .header__btn-text {
    color: #fff;
  }

  &__btn-img {
    position: absolute;
    top: 0;
    left: -28px;
    width: 100%;
    cursor: pointer;
  }


  &__info-price {
    color: #151515;
    font-size: 16px;
    font-family: 'Montserrat', Arial, sans-serif;
    line-height: 1.55;
    font-weight: 500;
    padding-bottom: 20px;
  }

  &__info-price span {
    font-weight: 700;
  }

  &__еxperience {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    list-style: none;
    width: 50%;
    padding-bottom: 60px;
  }

  &__experience-item {
    text-align: left;
    width: 33.333%;
    padding-right: 10px;
    padding-left: 15px;
  }

  .header__experience-item+.header__experience-item {
    border-left: 4px solid #ffcf00;
  }

  &__experience-old {
    color: #ffcf00;
    font-size: 46px;
    font-family: 'Montserrat', Arial, sans-serif;
    line-height: 1.55;
    font-weight: 600;
    background-position: center center;
    border-color: transparent;
    border-style: solid;
  }

  &__experience-info {
    color: #000000;
    font-size: 18px;
    font-family: 'Montserrat', Arial, sans-serif;
    line-height: 1.55;
    font-weight: 400;
    background-position: center center;
    border-color: transparent;
    border-style: solid;
  }
}

