  &__content {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 600px;
    background: #fff;
    padding: 20px;
    transform: translate(-50%, -50%);
  }

  &__close {
    position: absolute;
    top: -30px;
    right: 30px;
  }
}

